import React from "react";
import Button from "../Button/Button";
import "./Banner.css";
import logo from "../assets/logo.png";
import { NavLink  } from "react-router-dom";

const Banner = () => {
  return (
    <section>
      <div className=" content-element">
        <div className="image">
          {" "}
          <img src={logo} alt="logo upLexis" />
        </div>
        <div className="title">
          <h2>Histórico Empresarial</h2>
        </div>
      </div>
      <div className="text">
        <p>
          O aplicativo Histórico Empresarial permite ao usuário ter acesso a
          todos os fatos e acontecimentos relevantes de uma empresa desde o seu
          ano de fundaçâo.
        </p>
      </div>
      <div>
      <p>
        {" "}
        R$ <span>40,00</span>
      </p>{" "}
      <NavLink className="spaceF"  to="Detalhes" >{<Button text="Saiba Mais" />} </NavLink>
      </div>
     
    </section>
  );
};

export default Banner;
