import React from "react";
import { Link } from "react-router-dom";
import Banner from "../../Banner/Banner";
import "./Datails.css";
import Button from "../../Button/Button";
import rigth from "../../assets/r.png"
import left from "../../assets/l.png"
import grafico from "../../assets/grafico.png"

export default function Datails() {
  return (
    <div>
      <header
        style={{
          backgroundImage:
            "-webkit-linear-gradient(top left,#C7C1C9,#C7C1C9,#F5F5F5, #F5F5F5",
          color: "#000",
        }}
      >
        <Banner />
      </header>

      <Link to="/" className="back" end>&#706; Histórico Empresarial</Link>
      <div className="cards-layout">
        <div className="arrow">
          <img src={left} alt="" />
        </div>
        <div className="card">
          <div className="content-img"><img src={grafico} alt="" /></div>
          <div className="content-text">
            <p>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit.
              Repudiandae, culpa velit. Sit blanditiis ducimus minima, veniam.
            </p>
          </div>
        </div>

        <div className="card">
          <div className="content-img"><img src={grafico} alt="" /></div>
          <div className="content-text">
            <p>
              Lorem ipsum dolor sit amet, consectetur adipisicing elit.
              Repudiandae, culpa velit. Sit blanditiis ducimus minima, veniam
              facilis.
            </p>
          </div>
        </div>
        <div className="arrow">
        <img src={rigth} alt="" />
        </div>
      </div>
      <div className="datText">
      <p>
        {" "}
        O aplicativo Histórico Empresarial permite ao usuário ter acesso a todos
        os fatos e acontecimentos relevantes de uma empresa desde o seu ano de
        fundaçâo. Tenha acesso aos principais eventos corporativos de uma
        empresa, como marcos jurídicos, mudanças no quadro societario, aumento
        de capital,e muito mais.
      </p>
      <p>
        Apos realizar a consulta, o historico é salvo automaticamente, tornando
        as informações assesiveis ao usuario, a linha do tempo pode tambem ser
        exportada no formato PDF.
      </p>

      <p>
        
        R$ <span>40,00</span>
      </p>
      <Button text="Habilitar" name="btnDat"/>
      </div>
     
    </div>
  );
}
