import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "./Category.css";

import bag from "../assets/icons/bag-svgrepo-com.svg";
import all from "../assets/icons/global-svgrepo-com.svg";
import hammer from "../assets/icons/judge-hammer-svgrepo-com.svg";
import tree from "../assets/icons/pine-svgrepo-com.svg";
import circle from "../assets/icons/prohibited-svgrepo-com.svg";
import media from "../assets/icons/social-media-campaign-svgrepo-com.svg";
import piggy from "../assets/icons/prohibited-svgrepo-com.svg";
import diamond from "../assets/icons/diamond-svgrepo-com.svg";
import bank from "../assets/icons/bank-building-svgrepo-com.svg";
import man from "../assets/icons/man-svgrepo-com.svg";

const items = [{text:"Todos", img: <img src={all} alt="Todos" />  },
               {text:"Profissional", img: <img src={bag} alt="Profissional" />},
               {text:"Reguladore", img:<img src={bank} alt="Reguladore" />},
               {text:"Sócio Ambiental", img:<img src={tree} alt="Sócio Ambiental" />},
               {text: "Juridico", img:<img src={hammer} alt="Juridico" />},
               {text: "Listas Restritivas", img:<img src={circle} alt="Listas Restritivas" />},
               {text: "Mídia / Internet", img: <img src={media} alt="Mídia / Internet" />},
               {text: "Béns e imoveis", img:<img src={diamond} alt="Béns e imoveis" />},
               {text: "Cadastro", img:  <img src={man} alt="Cadastro" />},
               {text:"Financeiro", img:<img src={piggy} alt="Financeiro" />}];



export default function Category() {
 const cat = items.map((item,index) =>{
      const response=  
        <li key={index + 1}>
          {item.text}
          {item.img}
          </li>

          return response
  })

  
  return (  
    <>
      <nav className="menu">
        <ul >
            {cat}
        </ul>
      </nav>
    </>
  );
}
