import React from "react";
import "./Cards.css";
import {Link} from 'react-router-dom';


function Cards({ image, title, text }) {
  return (
    <div className="content-card">
      <img src={image} alt="" />
      <h2>{title}</h2>
      <p >
       {text}
      </p>
      <div className="info">
        <p>R$ 29,99</p>{" "}
        <a href="#">
          <Link className="spaceF" to="Detalhes">Saiba Mais</Link>
        </a>
      </div>
    </div>
  );
}

export default Cards;
