import React from 'react'
import "./Filter.css"

function Filter() {
    return (
        <div className="content">
            <label htmlFor="filter">Ordenar</label>
            <select name="filter" id="filter">
                <option value=""> Lançamento</option>
                <option value="">Preço</option>
               
            </select>
        </div>
    )
}

export default Filter
